<template>
  <div class="salary-stack-chart">
    <div class="chart-header">
      <div class="chart-title">【薪酬模式堆叠图】各城市不同薪资段岗位分布</div>
      
      <div class="filter-options">
        <span class="label">显示模式:</span>
        <div class="toggle-buttons">
          <button 
            class="toggle-btn" 
            :class="{ active: viewMode === 'stack' }" 
            @click="viewMode = 'stack'"
          >堆叠视图</button>
          <button 
            class="toggle-btn" 
            :class="{ active: viewMode === 'percentage' }" 
            @click="viewMode = 'percentage'"
          >百分比视图</button>
        </div>
      </div>
    </div>

    <!-- 图例区域 - 优化为交互式图例 -->
    <div class="legend-wrapper">
      <div 
        class="legend-item" 
        v-for="(city, idx) in cityList" 
        :key="city"
        @click="toggleCity(city)"
        :class="{ disabled: !activeCities.includes(city) }"
      >
        <span class="dot" :style="{ backgroundColor: colorList[idx % colorList.length] }"></span>
        <span class="legend-text">{{ city }}</span>
        <span class="status-indicator">
          <i v-if="activeCities.includes(city)" class="status-icon">✓</i>
        </span>
      </div>
    </div>

    <!-- 薪资段条形图 - 带动效果 -->
    <div class="bars-container">
      <div class="bar-group" v-for="(row, index) in displayData" :key="row.range">
        <div class="label-column">
          <div class="salary-label">{{ row.range }}</div>
        </div>
        <div class="bar-column">
          <div class="bar-content">
            <div
              v-for="(item, itemIndex) in row.items"
              :key="`${index}-${itemIndex}`"
              class="bar-block"
              :style="{
                width: item.percentage + '%',
                backgroundColor: colorList[item.index % colorList.length],
                transition: `width 0.5s ease-out ${itemIndex * 0.05}s`
              }"
              :title="`${item.city}: ${item.value}个职位 (${item.percentage.toFixed(1)}%)`"
            >
              <span class="bar-value" v-if="item.percentage > 10">{{ item.value }}</span>
            </div>
          </div>
        </div>
        <div class="count-column">
          <span class="count-value">{{ row.total }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'chartSalaryStack',
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      viewMode: 'stack', // 'stack' 或 'percentage'
      activeCities: [], // 活跃城市列表
      chartLoaded: false
    }
  },
  computed: {
    cityList() {
      if (!this.data || this.data.length === 0) return []
      const first = this.data[0]
      return Object.keys(first).filter(k => k !== 'salary_range')
    },
    colorList() {
      // 使用渐变色彩方案 - 更专业的配色
      return [
        '#1A6EFF', '#26A0FC', '#36C5D9', '#5AE5C5', 
        '#FFD166', '#FF9F40', '#FF6B6B', '#D95C86',
        '#7048E8', '#5D5FEF', '#3686FF', '#18C0C1'
      ]
    },
    displayData() {
      if (!this.data || this.data.length === 0) return []
      
      // 过滤有效城市
      const activeCities = this.activeCities.length ? this.activeCities : this.cityList
      
      return this.data.map(row => {
        // 为每个区间准备数据项
        const items = []
        let totalValue = 0
        
        // 计算总数
        activeCities.forEach(city => {
          totalValue += (row[city] || 0)
        })
        
        // 构建显示项
        activeCities.forEach((city, index) => {
          const value = row[city] || 0
          if (value === 0) return // 跳过值为0的城市
          
          const cityIndex = this.cityList.indexOf(city)
          let percentage = 0
          
          if (this.viewMode === 'stack') {
            // 堆叠视图：按堆叠比例显示
            percentage = totalValue > 0 ? (value / totalValue) * 100 : 0
          } else {
            // 百分比视图：各自占100%
            percentage = 100
          }
          
          items.push({
            city,
            value,
            percentage,
            index: cityIndex
          })
        })
        
        return {
          range: row.salary_range,
          items,
          total: totalValue
        }
      }).sort((a, b) => {
        // 按薪资区间排序，确保从低到高
        const getMinValue = (range) => {
          const match = range.match(/(\d+)/);
          return match ? parseInt(match[1]) : 0;
        }
        return getMinValue(a.range) - getMinValue(b.range);
      });
    }
  },
  watch: {
    cityList: {
      immediate: true,
      handler(list) {
        this.activeCities = [...list]
      }
    },
    data: {
      handler() {
        this.animateOnLoad()
      },
      immediate: true
    }
  },
  mounted() {
    this.animateOnLoad()
  },
  methods: {
    toggleCity(city) {
      if (this.activeCities.includes(city)) {
        if (this.activeCities.length > 1) { // 保证至少有一个城市激活
          this.activeCities = this.activeCities.filter(c => c !== city)
        }
      } else {
        this.activeCities.push(city)
      }
    },
    animateOnLoad() {
      if (this.data && this.data.length > 0 && !this.chartLoaded) {
        this.chartLoaded = true
        // 动画效果可以由CSS transition处理
      }
    }
  }
}
</script>

